import listCell from '@/components/mix-list-cell';
import api from '@/common/index.js';
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import {
	mapState
} from 'vuex';
let startY = 0,
	moveY = 0,
	pageAtTop = true,
	index = {
		username: '',
		uid: '',
		isvip: "",
		activeList: [],
		price: 0,
		integralNum: 0,
		disLength: 0
	};
export default {
	components: {
		listCell,
		uniNavBar
	},

	data() {
		return {
			navBar: true,
			app: 1,
			coverTransform: 'translateY(0px)',
			coverTransition: '0s',
			moving: false,
			language: '中文',
			langStatus: false,
			index: {
				username: '',
				uid: '',
				isvip: "",
				price: 0,
				integralNum: 0,
				disLength: 0
			}
		}
	},
	onLoad() {
		setTimeout(function() {
			const params = uni.getStorageSync('userinfo');
			if (!params) {
				this.$store.dispatch('showLogin');
				return false;
			}
		}, 300)
	},
	onShow() {
		let that = this;

		setTimeout(function() {
			index = that.index;
			const params = uni.getStorageSync('userinfo');
			index.username = params.username;
			index.uid = params.uid
			index.isvip = params.isvip;

			that.getAmt();
		}, 300)
		if (this.$store.state.app == "2") {
			this.app = 2
			const lang = uni.getStorageSync('lang');
			const params = uni.getStorageSync('userinfo');
			if (lang) {
				console.log(lang.locale)
				if (lang.locale == "zh") {
					this.language = "English";
					this.langStatus = false;
				}
				if (lang.locale == "en") {
					this.language = "中文";
					this.langStatus = false;
				}
			} else {
				this.$i18n.locale = 'en';
				this.language = "中文";
			}
			if (!params || !lang) {
				this.$store.dispatch('showLogin');
				return false;
			}
		}
	},




















	computed: {

		i18n() {
			return this.$t('my')
		}
	},
	methods: {
		delete1() {
			uni.removeStorage({
				key: 'lang',
				success: function(res) {

				}
			});
		},
		// ceshi(){
		// 	let type={
		// 		color:red
		// 	}
		// 	uni.setStorageSync('color', type);
		// },
		// 修改系统语言
		switchChange(e) {
			let that = this;
			let data = {
				locale: '',
				lang: ''
			};
			const userinfo = uni.getStorageSync('userinfo');
			if (this.language == "English") {
				this.language = "中文";
				this.langStatus = true
				this.$i18n.locale = 'en';
				userinfo.lang = 'English';
				data.lang = "English";
				data.locale = "en";
				// that.$store.dispatch('imghideZN');   //测试图片切换
			} else {
				this.langStatus = true
				this.language = "English";
				userinfo.lang = 'Chinese';
				data.lang = "Chinese";
				this.$i18n.locale = 'zh';
				data.locale = "zh";
				// that.$store.dispatch('imgshowEN'); //测试图片切换
			};
			// console.log(that.$store.getters.imgShow) //测试图片切换
			uni.setTabBarItem({
				index: 0,
				text: that.$t("index").tab1
			});
			uni.setTabBarItem({
				index: 1,
				text: that.$t("index").tab2
			});
			uni.setTabBarItem({
				index: 2,
				text: that.$t("index").tab3
			});
			uni.setTabBarItem({
				index: 3,
				text: that.$t("index").tab4
			});
			uni.setStorageSync('lang', data);
			uni.setStorageSync('userinfo', userinfo);
		},
		// 获取余额
		getAmt() {
			const params = uni.getStorageSync('userinfo');
			let that = this;
			that.$ajax.money(params).then(res => {
				index.price = res;
				that.integral();
			})
		},
		// 获取积分
		integral() {
			let that = this;
			that.$ajax.integral().then(res => {
				index.integralNum = res;
				that.disData();
			})
		},
		// 获取代金券列表
		disData() {
			let that = this;
			that.$ajax.dis().then(res => {
				let allList = res
				let list = []
				allList.forEach(function(item, index) {
					if (item.data.type.expire == 0) {
						if (item.data.type.not_used == 0) {
							list.push(item)
						}
					}

				})
				that.activeList = list;
				index.disLength = list.length;
				that.index = index;
			})
		},
		address(url) {
			uni.navigateTo({
				url: '/pages/address/address'
			})
		},
		order() {
			uni.navigateTo({
				url: '/pages/order/order'
			})
		},
		navTech() {
			uni.navigateTo({
				url: '/pages/technology/technology'
			})
		},
		repertory() {
			uni.navigateTo({
				url: '/pages/repertory/repertory'
			})
		},
		market() {
			uni.navigateTo({
				url: '/pages/market/market'
			})
		},
		sale() {
			uni.navigateTo({
				url: '/pages/sale/sale'
			})

		},
		navTo() {
			uni.navigateTo({
				url: '/pages/set/set'
			})
		},
		navCollect() {
			uni.navigateTo({
				url: '/pages/collect/collect'
			})
		},
		navDiscounts() {
			uni.navigateTo({
				url: '/pages/discounts/discounts'
			})
		},
		navTheme() {
			uni.reLaunch({
				url: '/pages/theme/theme'
			})
		},
		/**，
		 *  会员卡下拉和回弹
		 *  1.关闭bounce避免ios端下拉冲突
		 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
		 *    transition设置0.1秒延迟，让css来过渡这段空窗期
		 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
		 */
		coverTouchstart(e) {
			if (pageAtTop === false) {
				return;
			}
			this.coverTransition = 'transform .1s linear';
			startY = e.touches[0].clientY;
		},
		coverTouchmove(e) {
			moveY = e.touches[0].clientY;
			let moveDistance = moveY - startY;
			if (moveDistance < 0) {
				this.moving = false;
				return;
			}
			this.moving = true;
			if (moveDistance >= 80 && moveDistance < 100) {
				moveDistance = 80;
			}

			if (moveDistance > 0 && moveDistance <= 80) {
				this.coverTransform = `translateY(${moveDistance}px)`;
			}
		},
		coverTouchend() {
			if (this.moving === false) {
				return;
			}
			this.moving = false;
			this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
			this.coverTransform = 'translateY(0px)';
		}

	},
	onBackPress() {

		let main = plus.android.runtimeMainActivity();
		plus.android.runtimeMainActivity();
		plus.runtime.quit = function() {
			main.moveTaskToBack(false);
		};
		uni.closeSocket();
		uni.onSocketClose(function(res) {
			console.log('WebSocket 已关闭！');
		});
		plus.nativeUI.toast = (function(str) {
			if (str == '再按一次退出应用') {
				main.moveTaskToBack(false);
				return false;
			} else {
				uni.showToast({
					title: '再按一次退出应用',
					icon: 'none',
				})
			}
		});

	},
}
